/**
 *
 *  Classe parente de tous les exercices.
 *
 * @author Rmi Angot
 */
export default function Exercice () {
  // ///////////////////////////////////////////////
  // Autour de l'exercice
  // ///////////////////////////////////////////////
  this.titre = '' // Chane de caractre sans point  la fin. C'est le titre de l'exercice qui sera affich avec la rfrence dans le gnrateur d'exercices.
  this.boutonAide = false // Bouton en haut  droite des questions permettant d'afficher un pdf, texte, image, vido, contenu d'un autre site en "pop-up" via les fonctions modalXXXXXXX de outils.js.
  // Ci-dessous des exemples d'utilisation du this.boutonAide.
  // Noter que pour rcuprer le numeroExercice dont il est question, au lieu d'crire "this.nouvelleVersion = function () {" comme d'habitude, on crit "this.nouvelleVersion = function (numeroExercice) {".
  // this.boutonAide = modalImage(numeroExercice, urlImage, titre, labelBouton = 'Illustration', icone = 'image')
  // this.boutonAide = modalPdf(numeroExercice, urlPdf, titre='Aide', labelBouton = 'Aide - PDF', icone = 'file pdf')
  // this.boutonAide = modalTexteCourt(numeroExercice, texte, labelBouton = 'Aide', icone = 'info circle')
  // this.boutonAide = modalTexteLong(numeroExercice, titre, texte, labelBouton = 'Aide', icone = 'info circle')
  // this.boutonAide = modalUrl(numeroExercice, url, labelBouton = 'Aide', icone) pour afficher le contenu de url dans un iframe
  // this.boutonAide = modalVideo(numeroExercice, urlVideo, titre, labelBouton, icone)
  // this.boutonAide = modalYoutube(numeroExercice, idYoutube, titre, labelBouton = 'Aide - Vido', icone = 'youtube')

  // ///////////////////////////////////////////////
  // Construction de l'exercice
  // ///////////////////////////////////////////////
  this.consigne = '' // Chane de caractre qui apparat en gras au-dessus des questions de prfrence  l'infinitif et sans point  la fin.
  this.consigneCorrection = '' // Chane de caractre en gnral vide qui apparat au-dessus des corrections.
  this.introduction = '' // Texte qui n'est pas forcment en gras et qui apparat entre la consigne et les questions.
  this.listeQuestions = [] // Liste de chanes de caractres avec chacune correspondant  une question. Chaque question est dfinie par la mthode this.nouvelleVersion puis `listeDeQuestionToContenu(this)` mettra en forme `this.contenu` et `this.contenuCorrection` suivant `context` (sortie HTML ? diaporama ?...)
  this.listeCorrections = [] // Idem avec la correction.
  this.contenu = '' // Chane de caractres avec tout l'nonc de l'exercice construit  partir de `this.listeQuestions` suivant le `context`
  this.contenuCorrection = '' // Idem avec la correction
  this.autoCorrection = [] // Liste des objets par question pour correction interactive || export AMC.
  this.tableauSolutionsDuQcm = [] // Pour sauvegarder les solutions des QCM.

  // ///////////////////////////////////////////////
  // Mise en forme de l'exercice
  // ///////////////////////////////////////////////
  this.spacing = 1 // Interligne des questions
  this.spacingCorr = 1 // Interligne des rponses

  // ////////////////////////////////////////////
  // Gestion de la sortie LateX
  // ////////////////////////////////////////////
  this.pasDeVersionLatex = false // boolen qui indique qu'une sortie LateX est impossible.
  this.listePackages = [] // string ou liste de string avec le nom des packages spcifiques  ajouter dans le prambule.
  this.consigneModifiable = true // boolen pour dterminer si la consigne est modifiable en ligne dans la sortie LaTeX.
  this.nbQuestionsModifiable = true // boolen pour dterminer si le nombre de questions est modifiable en ligne.
  this.nbCols = 1 // Nombre de colonnes pour la sortie LaTeX des questions (environnement multicols).
  this.nbColsCorr = 1 // Nombre de colonnes pour la sortie LaTeX des rponses (environnement multicols).
  this.nbColsModifiable = true // boolen pour dterminer si le nombre de colonnes est modifiable en ligne dans la sortie LaTeX.
  this.nbColsCorrModifiable = true // boolen pour dterminer si le nombre de colonnes de la correction est modifiable en ligne dans la sortie LaTeX.
  this.spacingModifiable = true // boolen pour dterminer si l'espacement est modifiable en ligne dans la sortie LaTeX.
  this.spacingCorrModifiable = true // boolen pour dterminer si l'espacement est modifiable en ligne dans la sortie LaTeX.
  // this.vspace = -1 //Ajoute un \vspace{-1cm} avant l'nonc ce qui peut tre pratique pour des exercices avec des figures.

  // ////////////////////////////////////////////
  // Gestion de la sortie autre que LateX
  // ////////////////////////////////////////////
  this.beamer = false // boolen pour savoir si la sortie devra tre un diaporama beamer
  this.tailleDiaporama = 50 // Taille en pixels pour le calcul chronomtr.

  // ////////////////////////////////////////////
  // Paramtres
  // ////////////////////////////////////////////
  this.nbQuestions = 10 // Nombre de questions par dfaut (rcupr dans l'url avec le paramtre `,n=`)
  this.correctionDetailleeDisponible = false // boolen qui indique si une correction dtaille est disponible.
  this.correctionDetaillee = true // boolen indiquant si la correction dtaille doit tre affich par dfaut (rcupr dans l'url avec le paramtre `,cd=`).
  this.video = '' // Chaine de caractre pour un complment numrique (id Youtube, url, code iframe...).
  // Interactivit
  this.interactif = false // Exercice sans saisie utilisateur par dfaut.
  this.interactifObligatoire = false // Certains exercices sont uniquement des QCM et n'ont pas de version non interactive.
  // Ajoute un formulaire de paramtrage par l'utilisateur rcupr via this.sup ou dans le paramtre d'url ',s='
  this.besoinFormulaireNumerique = false // Sinon this.besoinFormulaireNumerique = [texte, max, tooltip facultatif]
  this.besoinFormulaireTexte = false // Sinon this.besoinFormulaireTexte = [texte, tooltip]
  this.besoinFormulaireCaseACocher = false // Sinon this.besoinFormulaireCaseACocher = [texte]
  // Ajoute un formulaire de paramtrage par l'utilisateur rcupr via this.sup2 ou dans le paramtre d'url ',s2='
  this.besoinFormulaire2Numerique = false // Sinon this.besoinFormulaire2Numerique = [texte, max, tooltip facultatif]
  this.besoinFormulaire2Texte = false // Sinon this.besoinFormulaire2Texte = [texte, tooltip]
  this.besoinFormulaire2CaseACocher = false // Sinon this.besoinFormulaire2CaseACocher = [texte]
  // Ajoute un formulaire de paramtrage par l'utilisateur rcupr via this.sup3 ou dans le paramtre d'url ',s3='
  this.besoinFormulaire3Numerique = false // Sinon this.besoinFormulaire3Numerique = [texte, max, tooltip facultatif]
  this.besoinFormulaire3Texte = false // Sinon this.besoinFormulaire3Texte = [texte, tooltip]
  this.besoinFormulaire3CaseACocher = false // Sinon this.besoinFormulaire3CaseACocher = [texte]

  // ///////////////////////////////////////////////
  // Exercice avec des dpendances particulires
  // ///////////////////////////////////////////////
  // this.typeExercice = 'MG32' // Pour charger MathGraph32.
  this.mg32Editable = false // Les figures MG32 ne sont pas interactives par dfaut.
  // this.dimensionsDivMg32 = [500, 450] // Dimensions du SVG cr par MathGraph32.
  // this.typeExercice = 'Scratch' // Pour charger Scratchblocks.
  // this.typeExercice = 'IEP' // Pour charger InstrumEnPoche.
  // this.typeExercice = 'dnb' // Ce nest pas un exercice alatoire il est trait diffremment. Les exercices DNB sont des images pour la sortie Html et du code LaTeX statique pour la sortie latex.
  // this.typeExercice = 'XCas' // Pour charger le JavaScript de XCas qui provient de https://www-fourier.ujf-grenoble.fr/~parisse/giac_fr.html
  this.listeArguments = []

  this.nouvelleVersion = function (numeroExercice) {}

  /**
   *
   * @param {int} i indice de la question
   * @param  {...any} args toutes les variables pertinentes qui "rsumeraient" la question
   * @returns {boolean} true si la question n'a jamais t pose
   */
  this.questionJamaisPosee = function (i, ...args) {
    if (i === 0) this.listeArguments = []
    let argsConcatenes = ''
    for (const arg of args) {
      if (arg !== undefined) argsConcatenes += arg.toString()
    }
    if (this.listeArguments.indexOf(argsConcatenes) > -1) {
      return false
    } else {
      this.listeArguments.push(argsConcatenes)
      return true
    }
  }
}
